nav {
  background-color: #000;
  display: flex;
}

nav a {
  color: #FFF;
  display: block;
  border-left: 1px solid #313131;
  border-right: 1px solid #313131;
  padding: 1.5rem 0;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  flex: 1;
  text-align: center;
  letter-spacing: 0.2rem;
  font-size: 0.8rem;
  border-bottom: 2px solid #313131;
  max-height: 60px;
}

nav a.brand {
  flex: 0.8;
}

nav a.brand,
nav a.brand:hover {
  background: none;
  border-bottom: 2px solid #313131;
}

nav a.brand img,
nav a.brand:hover img {
  display: inherit;
  text-align: center;
  max-width: 90%;
  margin: -0.25rem auto;
  height: 22px;
  padding: 0.2rem;
}

nav a.blog {
  flex: 0.5;
}

nav a.blog.active {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  border-left: none;
  border-bottom: none;
}

nav a:last-child {
  border-right: none;
}

nav a:first-child {
  border-left: none;
}

nav a:hover {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  color: #FFF;
  border-bottom: none;
}

nav a.blog:hover {
  background: #FFF;
}

@supports (mix-blend-mode: lighten) {
  nav a.blog:hover span {
    display: inline-block;
    position: relative;
    color: #000;
    background: #fff;
    mix-blend-mode: multiply;
  }

  nav a.blog:hover span::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
    pointer-events: none;
  }

  nav a.blog:hover span::before {
    mix-blend-mode: screen;
  }
}

#company {
