.pagination {
  padding-top: 1rem;
  padding-bottom: 3rem;
  font-family: "Montserrat Light", "Helvetica Neue", Arial, sans-serif;
}

.pagination .arrow_box {
  transition: inherit;
  margin-top: -0.33rem;
  position: relative;
  background: #959595;
  border: 0.75px solid #959595;
  width: 100%;
}

.pagination .arrow_box:after,
.pagination .arrow_box:before {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.pagination .arrow_box:after {
  border-color: transparent;
  border-width: 3px;
  margin-top: -3px;
}

.pagination .arrow_box:before {
  border-color: transparent;
  border-width: 4px;
  margin-top: -4px;
}

.pagination .arrow_box_right:after,
.pagination .arrow_box_right:before {
  left: 100%;
  top: 50%;
}

.pagination .arrow_box_right:after {
  border-left-color: #959595;
}

.pagination .arrow_box_right:before {
  border-left-color: #959595;
}

.pagination .arrow_box_left:after,
.pagination .arrow_box_left:before {
  right: 100%;
  top: 50%;
}

.pagination .arrow_box_left:after {
  border-right-color: #959595;
}

.pagination .arrow_box_left:before {
  border-right-color: #959595;
}

.pagination .next-page {
  float: right;
}

.pagination a {
  font-size: 0.8rem;
  transition: width 1s, margin-left 1s, margin-right 1s;
}

.pagination a .arrow {
  transition: inherit;
  display: inline-block;
  width: 24px;
}

.pagination a:hover,
.pagination a:visited {
  color: #959595;
}

.pagination a:hover .arrow,
.pagination a:visited .arrow {
  width: 32px;
}

.pagination a.previous-page:hover .arrow {
  margin-left: -8px;
}

.pagination a.next-page:hover {
  margin-right: -8px;
}

@media screen and (max-width: 1200px) {
  .pagination .next-page {
    margin-right: 0.8rem;
  }

  .pagination .previous-page {
    margin-left: 0.8rem;
  }
}

