.pagination{
  padding-top: 1rem;
  padding-bottom: 3rem;
  @include arrows($grey, -0.33rem, 3px);
  font-family: "Montserrat Light", "Helvetica Neue", Arial, sans-serif;
  .next-page{
    float: right;
  }
  a{
    font-size: 0.8rem;
    transition: width 1s, margin-left 1s, margin-right 1s;
    .arrow{
      transition: inherit;
      display: inline-block;
      width: 24px;
    }
    &:hover, &:visited{
      color: $grey;
      .arrow{
        width: 32px;
      }
    }
    &.previous-page:hover{
      .arrow{
        margin-left: -8px;
      }
    }
    &.next-page:hover{
      margin-right: -8px;

    }
  }
}

@media screen and (max-width: 1200px) {
  .pagination {
    .next-page{
      margin-right: 0.8rem;
    }
    .previous-page{
      margin-left: 0.8rem;
    }
  }
}


